import React, { createContext, useEffect, useState } from "react";
import { useSelector, useDispatch } from "react-redux";
import Services from "../jsx/_services";
import actions from "../jsx/_actions";
import helpers from "../jsx/_helpers";
import useService from "../jsx/_hooks/service.hook";
import { createBrowserHistory } from "history";

export const ServiceContext = createContext();

export default function ServiceContextProvider(props) {
  const session = useSelector((state) => state?.session);
  const dispatch = useDispatch();
  const history = createBrowserHistory();

  history.listen(({ location, action }) => {
    // console.log(location, action);
    // switch (String(action)?.toLowerCase()) {
    //   case "push": {
    //     // history.push(
    //     //   `${location?.pathname}${location.search}${location.hash}`,
    //     //   location?.state
    //     // );
    //   }
    // }
  });
  const [services, setServices] = useState(null);
  const { user: userAction } = actions;

  useEffect(() => {
    let newService = new Services(
      {
        token: session?.user?.token || "",
        baseURL: "/api",
        timeout: 10000,
      },
      () => dispatch(userAction.logout())
    );
    setServices(newService);
    // dispatch(service.save(newService));
  }, [session]);

  return (
    <ServiceContext.Provider
      value={{ services, session, useService, history, actions, helpers }}
    >
      {props.children}
    </ServiceContext.Provider>
  );
}
